@use "global" as *;


.contents {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: rem(118);

  @include mq(xl) {
    gap: rem(80);
  }

  @include mq(md) {
    flex-direction: column;
    align-items: center;
    gap: rem(64);
  }
}

.contents__item {
  @include mq(md) {
    width: 100%;
  }
}
