@use "global" as *;


.content {

}

.content__img-wrap {
  overflow: hidden;
  filter: drop-shadow(4px 4px 54px rgba(0, 0, 0, 0.15));
  border-radius: rem(230) rem(230) 0 0;

  @include mq(md) {
    width: fit-content;
    border-radius: rem(144) rem(144) 0 0;
    margin-inline: auto;
  }
}

.content__img {
  overflow: hidden;

  @include mq(md) {
    width: fit-content;
  }
}

.content__img img {
  aspect-ratio: 480 / 517;
  max-width: rem(480);
  width: 100%;
  object-fit: cover;

  @include mq(md) {
    aspect-ratio: 296 / 204;
    max-width: rem(296);
    width: 100%;
    margin-inline: auto;
  }
}

.content__menu {
  margin-top: rem(50);
}

.content__menu-top {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include mq(md) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.content__menu-title {
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(25);
  font-weight: $bold;
  line-height: 1.4;

  @include mq(md) {
    font-size: rem(20);
  }
}

.content__menu-link {
  padding-left: rem(25);  // 『￥』のアイコンが表示されるスペースを確保
  position: relative;
  transition: all 0.2s ease-out;
}

.content__menu-link:hover {  // hover
  opacity: 0.6;
}

.content__menu-link::before {  // 『￥』のアイコン
  content: "";
  aspect-ratio: 1 / 1;
  width: rem(20);
  height: rem(20);
  background: url(../images/common/menu-link-yen.webp) no-repeat center center / contain;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(calc(-50% + 2px));  // 『1px』はアイコンの位置調整
}

.content__menu-text {
  leading-trim: both;
  text-edge: cap;
  font-size: rem(14);
  font-weight: $bold;
  line-height: 1.4;
  position: relative;
}

.content__menu-text::after {  // 下線
  content: "";
  width: 100%;
  height: 1px;
  background-color: #7B695A;
  position: absolute;
  bottom: rem(-5);
  left: 50%;
  transform: translateX(-50%);
}

.content__menu-list {
  margin-top: rem(29);
}

.content__btn-sp {
  display: none;

  @include mq(md) {
    display: block;
    width: fit-content;
    margin-top: rem(35);
    margin-inline: auto;
  }
}

.content__btn {
  display: none;

  @include mq(md) {
    display: block;
    margin-top: rem(35);
    min-width: rem(240);
  }
}
