@use "global" as *;


.menu {
  padding-top: rem(75);
  padding-bottom: rem(80);
  background: url(../images/common/menu_bg-pc.webp) no-repeat center center / cover;
}

.menu__inner {
  @include mq(md) {
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

.menu__wrap {

}

.menu__title {
  margin-inline: auto;
}

.menu__contents {
  margin-top: rem(32);
}

.menu__btn-wrap {
  margin-top: rem(48);
  width: fit-content;
  margin-inline: auto;

  @include mq(md) {
    display: none;
  }
}

.menu__btn {
  min-width: rem(251);

  @include mq(md) {
    margin-top: rem(20);
    min-width: rem(240);
  }
}
