@use "global" as *;


.list {
  border-top: 1px solid #7B695A;
  border-bottom: 1px solid #7B695A;

  @include mq(md) {
    border: none;
  }
}

.list__item {
  display: block;
  padding-block: rem(20);
  border-bottom: 1px solid #EED5CE;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 4.36%;
}

.list__item:hover .list__item-arrow {  // hover
  transform: translateX(rem(5));
}

.list__item:last-of-type {
  border-bottom: none;
}

.list__item-text {
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;

  @include mq(md) {
    font-size: rem(14);
  }
}

.list__item-arrow {
  transition: transform 0.2s ease-out;
}
